.App{

  padding:20px;
}

body{
  background-color: #f6f6f6;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin:0px;
}


@media (min-width:650px){
  .App{
    width:640px;
  }
}

@media (max-width:650px){
  .App{
    width:90vw;
  }
}

#root{
  background-color: white;
  box-shadow: rgba(100,100,111,0.2) 0px 7px 29px 0px;
}

.App{
  min-height:100vh;
  padding-left:20px;
  padding-right:20px;
}

/* MyButton */

.MyButton{
  cursor:pointer;
  border:none;
  border-radius:5px;

  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;

  font-size: 18px;

  white-space: nowrap;
  /* 두줄이 안되게 만들어주는 거 */
  
}

.MyButton_default{
  background-color: #ececec;
  color:black
}

.MyButton_positive{
  background-color: #64c964;
  color:white;

}

.MyButton_negative{
  background-color: #fd565f;
  color:white;
}

header{

  padding-top: 20px;
  padding-bottom:20px;

  display:flex;
  align-items: center;
  border-bottom:1px solid #e2e2e2
}

header > div{
  display: flex;
}
header .head_text{
  width:50%;
  font-size:25px;
  justify-content: center;
}

header .head_btn_left{
  width:25%;
  justify-content: start;
}

header .head_btn_right{
  width:25%;
  justify-content: end;
}


.DiaryList .menu_wrapper{
  margin-top:20px;
  margin-bottom:30px;

  display: flex;
  justify-content: space-between;
}

.DiaryList .menu_wrapper .right_col{
  flex-grow:1;
}

.DiaryList .menu_wrapper .right_col button{
  width:100%;
}

.DiaryList .ControlMenu{
  margin-right:10px;
  border:none;
  border-radius:5px;
  background-color: #ececec;

  padding:10px 20px 10px 20px;
  cursor: pointer;
}


.DiaryItem{

  padding-top:15px;
  padding-bottom:15px;

  border-bottom: 1px solid #e2e2e2;
  display: flex;
  justify-content: space-between;
}

.DiaryItem .emotion_img_wrapper{
  cursor:pointer;
  min-width:120px;
  height:80px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
}

.DiaryItem .emotion_img_wrapper_1{
  background-color: #64c964
}
.DiaryItem .emotion_img_wrapper_2{
  background-color: #9dd772
}
.DiaryItem .emotion_img_wrapper_3{
  background-color: #fdce17;}
.DiaryItem .emotion_img_wrapper_4{
    background-color: #fd8446;}
    .DiaryItem .emotion_img_wrapper_5{
      background-color: #fd565f;}


.DiaryItem .info_wrapper{
  flex-grow: 1;
  cursor: pointer;
  margin-left:20px;
}

.DiaryItem .diary_date{
  font-weight: bold;
  font-size:25px;
  margin-bottom:5px;
}

.DiaryItem .btn_wrapper{
  min-width:70px;
}

.DiaryEditor{

}

.DiaryEditor section{
  margin-bottom:40px;
}
.DiaryEditor h4{
  font-size:22px;
  font-weight: bold;
}

.DiaryEditor .input_date{
  border:none;
  border-radius: 5px;;
  background-color: #ececec;
  cursor: pointer;
  padding:10px 20px 10px 20px;
}

.EmotionItem{
  cursor:pointer;
  border-radius: 5px;;
  padding-top:20px;
  padding-bottom:20px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.DiaryEditor .emotion_list_wrapper{
  display: grid;
  grid-template-columns: repeat(5,auto);
  gap:2%;

}

.EmotionItem img{
  width:50%;
  margin-bottom: 10px;
}

.EmotionItem span{
  font-size:18px;
}

.Emotionitem_off{
  background-color: #ececec;
}

.EmotionItem_on_1{
  background-color: #64c964;
  color:white
}
.EmotionItem_on_2{
  background-color: #9dd772;
  color:white
}
.EmotionItem_on_3{
  background-color: #fdce17;
  color:white
}
.EmotionItem_on_4{
  background-color: #fd8446;
  color:white
}
.EmotionItem_on_5{
  background-color: #fd565f;
  color:white
}

.DiaryEditor textarea{
  font-size:20px;
  
  box-sizing: border-box;
  width:100%;
  min-height: 200px;
  resize: vertical;

  border: none;
  border-radius: 5px;
  background-color: #ececec;

  padding:20px;
}

.DiaryEditor .control_box{

  display: flex;
  justify-content: space-between;
}


.DiaryPage{

}

.DiaryPage section{
  width:100%;
  margin-bottom: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.DiaryPage h4{
  font-size:22px;
  font-weight:bold;
}

.DiaryPage .diary_img_wrapper{
  background-color: #ececec;
  width:250px;
  height:250px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.DiaryPage .emotion_descript{
  font-size:25px
}


.DiaryPage .diary_img_wrapper_1{
  background-color: #64c964;
  color:white
}
.DiaryPage .diary_img_wrapper_2{
  background-color: #9dd772;
  color:white
}
.DiaryPage .diary_img_wrapper_3{
  background-color: #fdce17;
  color:white
}
.DiaryPage .diary_img_wrapper_4{
  background-color: #fd8446;
  color:white
}
.DiaryPage .diary_img_wrapper_5{
  background-color: #fd565f;
  color:white
}

.DiaryPage .diary_content_wrapper{
  width: 100%;
  height:auto;
  background-color: #ececec;
  border-radius: 5px;
  word-break: keep-all;
  overflow-wrap: break-word;
}
.DiaryPage .diary_content_wrapper p{
  padding:20px;
  text-align: left;
  font-size: 20px;
  font-weight: 400;
  line-height: 2.5;
}